<template>
  <!-- HowDoWork -->
  <div class="howDoWorks">
    <div class="container">
      <h1>{{$t('work.work')}}</h1>
      <p class="content">
        <pre>{{$t('work.content')}}</pre>
      </p>
    </div>
  </div>
